import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AddIcon } from "@chakra-ui/icons";
import { FatchMyChats } from "../../Apifunctions/API";
import { Box, Button, Stack, Text } from "@chakra-ui/react";
import { initializeSelectedChat } from "../../store/slice";
import { getSender,GetNameofSender, CheksEmptyObject } from "../../Functions/fun";
import GroupChatModal from "./GroupChatModal";
import ChatLoading from "../ChatLoading";

const MyChats = () => {
  const { Chats, SelectedChat, CreatedChat } = useSelector(
    (state) => state.ChatApp
  );
  const User = JSON.parse(localStorage.getItem('user'));

  const dispatch = useDispatch();
  useEffect(() => {
    FatchMyChats(dispatch);
  }, [CreatedChat,SelectedChat]);

  return (
    <>
      <Box
        display={{ base: CheksEmptyObject(SelectedChat) ? "flex" : "none", md: "flex" }}
        flexDir="column"
        alignItems="center"
        p={3}
        bg="white"
        maxW={{ base: "100%", md: "31%" }}
        borderRadius="lg"
        borderWidth="1px"
      >
        <Box
          pb={3}
          px={3}
          fontSize={{ base: "28px", md: "30px" }}
          fontFamily="Work sans"
          display="flex"
          w="100%"
          justifyContent="space-between"
          alignItems="center"
        >
          My Chats
      <GroupChatModal>
          <Button
            d="flex"
            fontSize={{ base: "17px", md: "10px", lg: "17px" }}
            rightIcon={<AddIcon />}
          >
            New Group Chat
          </Button>
          </GroupChatModal>
        </Box>
        <Box
          d="flex"
          flexDir="column"
          p={3}
          bg="#F8F8F8"
          w="100%"
          h="100%"
          borderRadius="lg"
          overflowY="hidden"
        >
          {Chats ? (
            <Stack overflowY="scroll">
              {Chats.map((chat) => (
                <Box
                  onClick={() => dispatch(initializeSelectedChat(chat))}
                  cursor="pointer"
                  bg={SelectedChat === chat ? "#38B2AC" : "#E8E8E8"}
                  color={SelectedChat === chat ? "white" : "black"}
                  px={3}
                  py={2}
                  borderRadius="lg"
                  key={chat._id}
                >
                  <Text>
                    {!chat.isGroupChat
                      ? getSender(User,chat.users)
                      : chat.chatName}
                  </Text>
                  {chat.latestMessage && (
                    <Text fontSize="xs">
                      <b>{chat.latestMessage.sender.name} : </b>
                      {chat.latestMessage.content.length > 50
                        ? chat.latestMessage.content.substring(0, 51) + "..."
                        : chat.latestMessage.content}
                    </Text>
                  )}
                </Box>
              ))}
            </Stack>
          ) : (
            <ChatLoading/>
          )}
        </Box>
      </Box>
    </>
  );
};

export default MyChats;
