import {
  Box,
  Button,
  chakra,
  FormControl,
  FormLabel,
  Input,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalFooter,
  ModalHeader,
  ModalOverlay,
  Text,
  useDisclosure,
  useToast,
} from "@chakra-ui/react";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { SearchUsers,CreateGroup } from "../../Apifunctions/API";
import UserListItems from "../../components/chats/UserListItems";
import { initializeSelectedChat, initializeSelectedUser, initializeUsers } from "../../store/slice";
import ChatLoading from "../ChatLoading";
import UserBadgeItem from "../UserBadgeItem";

const GroupChatModal = ({ children }) => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [GroupName, setGroupName] = useState("");
  const [Query, setSearchUser] = useState("");
  const { Users, Loading, SelectedUser,Chats } = useSelector(
    (state) => state.ChatApp
  );
  const dispatch = useDispatch();
  const toast = useToast();

  useEffect(() => {
    if (!isOpen) {
      dispatch(initializeSelectedUser([]));
      dispatch(initializeUsers([]));
      setSearchUser("");
    }
  }, [isOpen]);

  const HandleSearchUser = (query) => {
    setSearchUser(query);
    SearchUsers(query, dispatch);
  };

  const AddToGroup = (addtogroupuser) => {
    if (SelectedUser.includes(addtogroupuser)) {
      toast({
        title: "You already selected this user !",
        status: "warning",
        isClosable: true,
        duration: 2500,
        position: "top",
      });
      return;
    }
    toast({
      title: "Selected !",
      status: "success",
      isClosable: true,
      duration: 2500,
      position: "top",
    });
    dispatch(initializeSelectedUser([...SelectedUser, addtogroupuser]));
  };

  const RemoveUser = (user) => {
    const AfterRemove = SelectedUser.filter((u) => u._id !== user._id);
    dispatch(initializeSelectedUser(AfterRemove));
  };

  return (
    <Box>
      <span onClick={onOpen}>{children}</span>
      <Modal blockScrollOnMount={false} isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader textAlign={"center"}>Create Group Chat</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <FormControl>
              <Input
                type={"text"}
                mb="4"
                value={GroupName}
                onChange={(e) => setGroupName(e.target.value)}
                placeholder="Enter Group Name"
              />
            </FormControl>
            <FormControl>
              <Input
                type={"text"}
                value={Query}
                onChange={(e) => HandleSearchUser(e.target.value)}
                placeholder="Search Users e.g joe Doe, Hemant Sonawane"
              />
            </FormControl>
            {SelectedUser && (
              <Box
                display={"flex"}
                p="2"
                overflowX={"scroll"}
                alignItems={"center"}
                w="100%"
              >
                {SelectedUser.map((u) => {
                  return (
                    <UserBadgeItem
                      User={u}
                      key={u._id}
                      HandleClick={() => RemoveUser(u)}
                    />
                  );
                })}
              </Box>
            )}

            {!Loading ? (
              Users?.map((user) => {
                return (
                  <UserListItems
                    User={user}
                    key={user._id}
                    HandleClick={() => AddToGroup(user)}
                  />
                );
              })
            ) : (
              <ChatLoading />
            )}
          </ModalBody>
          <ModalFooter>
            <Button colorScheme="blue" onClick={()=> CreateGroup(SelectedUser,GroupName,Chats,dispatch,onClose)} >
              Create Chat
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </Box>
  );
};

export default GroupChatModal;
