import { Avatar, Box, Text, Tooltip } from "@chakra-ui/react";
import React from "react";
import { IslastMessage, isSameSender } from "../../Functions/fun";

const MessageCompo = ({ messages, message, index }) => {
  
  const User = JSON.parse(localStorage.getItem("user"));
  return (
    <div style={{flexShrink:"0" }} >
      {isSameSender(message, User._id) ? (
        <Box display="flex" justifyContent={"end"}>
          <Text
            background={"white"}
            borderRadius={"1em"}
            padding={"0.4em 0.8em"}
            m={"0.3em 0em"}
          >
            {message.content}
          </Text>{" "}
        </Box>
      ) : (
        <Box display="flex" justifyContent={"start"}>
          {" "}
          <div  style={{background:"white",display:"flex", justifyContent:"space-between", alignItems:"center", margin:"2px",  padding:"0.4em 0.6em", borderRadius:"1.5em"}}>
          {IslastMessage(messages,message,index) && <Avatar boxSize={"1.5em"}   src={message.sender.pic?.url}></Avatar>}
          <Text
            background={"white"}
            m={"0px 0.5em"}
          >
            {message.content}
          </Text>{" "}
          </div>
        </Box>
      )}
    </div>
  );
};

export default MessageCompo;
