import {
  Button,
  FormControl,
  FormLabel,
  Input,
  InputGroup,
  InputRightElement,
  VStack,
} from "@chakra-ui/react";
import React, { useEffect, useState } from "react";
import { SignUpUser } from "../../Apifunctions/API";
import { useToast } from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { CheksEmptyObject } from "../../Functions/fun";

const Signup = () => {
  const [Show, SetShow] = useState(false);
  const [Showtwo, SetShowtwo] = useState(false);

  const [Name, SetName] = useState("");
  const [Email, SetEmail] = useState("");
  const [Password, SetPassword] = useState("");
  const [ComfirmPassword, SetComfirmPassword] = useState("");
  const [Pic, SetPic] = useState([]);
  const [Current, SetCurrent] = useState(false);
  const toast = useToast();
  const dispatch = useDispatch();
  const {  Error } = useSelector((state) => state.ChatApp);
  const User = JSON.parse(localStorage.getItem('user'));
  const navigate = useNavigate();

  useEffect(() => {
    if (User?._id) {
      navigate("/chats");
      return;

    } else if ( !CheksEmptyObject(Error) && Current) {
      toast({
        title: "Error",
        description: `${Error}`,
        status: "error",
        duration: 2500,
        isClosable: true,
      });
    }
  }, [User, Error, navigate, toast,Current]);

  const HandleImage = (e) => {
    const Image = Array.from(e.target.files);
    const reader = new FileReader();
    reader.onload = function () {
      if (reader.readyState === 2) {
        SetPic([reader.result]);
      }
    };
    reader.readAsDataURL(Image[0]);
  };

  const SubmitHandler = () => {
    if (!Name || !Email || !Password || !ComfirmPassword) {
      toast({
        title: "Error",
        description: `Please Enter All Fields`,
        status: "warning",
        duration: 2500,
        isClosable: true,
      });

      return;
    }
    if(Password !== ComfirmPassword){
      toast({
        title: "warning",
        description: `Please Enter Correct Password`,
        status: "warning",
        duration: 2500,
        isClosable: true,
      });
      return;
    }
    SetCurrent(true);
    const data = {
      name: Name,
      email: Email,
      password: Password,
      pic: Pic,
    };
    SignUpUser(data, dispatch);
  };

  return (
    <VStack spacing={4}>
      <FormControl id="first-name" isRequired>
        <FormLabel>Name</FormLabel>
        <Input
          placeholder="Enter your Name"
          value={Name}
          onChange={(e) => SetName(e.target.value)}
        />
      </FormControl>
      <FormControl id="first-email-signup" isRequired>
        <FormLabel>Email</FormLabel>
        <Input
          placeholder="Enter your Email"
          type={"email"}
          value={Email}
          onChange={(e) => SetEmail(e.target.value)}
        />
      </FormControl>

      <FormControl id="first-password-signup" isRequired>
        <FormLabel>Password</FormLabel>
        <InputGroup>
          <Input
            placeholder="Enter your Password"
            value={Password}
            type={Show ? "text" : "password"}
            onChange={(e) => SetPassword(e.target.value)}
          />
          <InputRightElement width={"4rem"}>
            <Button
              fontFamily={"work sans"}
              onClick={() => SetShow((state)=> !state)}
            >
              {Show ? "Hide" : "Show"}
            </Button>
          </InputRightElement>
        </InputGroup>
      </FormControl>
      <FormControl id="first-comfimpassword-signup" isRequired>
        <FormLabel>Comfirm Password</FormLabel>
        <InputGroup>
          <Input
            placeholder="Enter your Password"
            value={ComfirmPassword}
            type={Showtwo ? "text" : "password"}
            onChange={(e) => SetComfirmPassword(e.target.value)}
          />
          <InputRightElement width={"4rem"}>
            <Button
              fontFamily={"work sans"}
              onClick={() => SetShowtwo((state) => !state)}
            >
              {Showtwo ? "Hide" : "Show"}
            </Button>
          </InputRightElement>
        </InputGroup>
      </FormControl>

      <FormControl id="first-name" isRequired>
        <FormLabel>Profile Image</FormLabel>
        <Input
          type={"file"}
          accept={"image/"}
          multiple={false}
          p={"2"}
          placeholder="Enter your Image"
          onChange={(e) => HandleImage(e)}
        />
      </FormControl>

      <Button
        colorScheme={"blue"}
        onClick={() => SubmitHandler()}
        color="white"
        width={"100%"}
        style={{ marginTop: 15 }}
      >
        Sign Up
      </Button>
    </VStack>
  );
};

export default Signup;
