export const GetNameofSender = (LogedinUser,Users) => { 
    
 const User = Users?.filter((u)=> u._id !== LogedinUser._id)
 
 return  User && User[0]?.name
}


export const GetChatOrGroupImg = (selectedchat,LogedinUser) => {
  if(!selectedchat.isGroupChat){
   return selectedchat.users[0]?._id === LogedinUser._id ? selectedchat.users[0]?.pic?.url : selectedchat.users[1]?.pic?.url
  }
}


export const CheksEmptyObject = (obj) => {
  return obj && Object.keys(obj).length === 0 && Object.getPrototypeOf(obj) === Object.prototype 
}



export const IslastMessage = ( messages, message,index) => {

  return message?.sender._id ===  messages[Number(index + 1)]?.sender._id ? false: true ;
}

export const isSameSender = (message,userId) => {
   return message.sender._id === userId ? true : false ;
}

export const KeepScrollUpChat = (scroll) => {
  return  scroll.current?.scrollTop === 0 || scroll.current?.scrollTop ? scroll.current.scrollTop = scroll.current.scrollHeight : "";
  }
