import { CloseIcon } from '@chakra-ui/icons'
import { Box, Text } from '@chakra-ui/react'
import React from 'react'

const UserBadgeItem = ({User , HandleClick}) => {
  return (
  
         <Box flexShrink={'0'} display={'flex'}  p='1.5' onClick={HandleClick} mr={'2'} color={'white'} backgroundColor={'blue'} justifyContent='space-between' alignItems={'center'} ><Text fontFamily={'work sans'} fontSize='small' fontWeight={'medium'} >{User.name}</Text>  <CloseIcon ml={'2'} cursor="pointer"  boxSize={'3'} /> </Box>
    
  )
}

export default UserBadgeItem