import {
  Box,
  Button,
  flattenTokens,
  FormControl,
  Input,
  Text,
  Tooltip,
  useToast,
} from "@chakra-ui/react";
import { ArrowBackIcon, ViewIcon } from "@chakra-ui/icons";
import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getSender,
  GetNameofSender,
  CheksEmptyObject,
  KeepScrollUpChat,
} from "../../Functions/fun";
import ViewChatDetails from "./ViewChatDetails";
import UpdateGroupChat from "./UpdateGroupChat";
import ChatLoading from "../ChatLoading";
import { GetAllMessages, SendMessage } from "../../Apifunctions/API";
import MessageCompo from "./MessageCompo";
import "./ChatBox.css";
import {
  initializeLoding,
  initializeMessages,
  initializeNotification,
  initializeSelectedChat,
} from "../../store/slice";
import io from "socket.io-client";
var socket, compareselectedchat;

const ChatBox = () => {
  const { SelectedChat, Loading, ChatMessages, Notification, Error } =
    useSelector((state) => state.ChatApp);
  const [NewMessage, setNewMessgae] = useState("");
  const [Message, setMessgae] = useState([]);
  const [Typing, setTypinge] = useState(false);

  const [SocketConnected, setSocketConnected] = useState(false);
  const dispatch = useDispatch();
  const toast = useToast();
  const User = JSON.parse(localStorage.getItem("user"));
  const scroll = useRef(null);
  const ENDPOINT = "http://localhost:3737/";
  socket = io(ENDPOINT);

  useEffect(() => {
    socket.emit("setup", User);
    socket.on("connected", () => {
      setSocketConnected(true);
    });
    socket.on("typing", () => setTypinge(true));
    socket.on("stop typing", () => setTypinge(false));
  }, []);

  useEffect(() => {
    if (!CheksEmptyObject(SelectedChat)) {
      GetAllMessages(SelectedChat, setMessgae, dispatch);
      socket.emit("join chat", SelectedChat._id);
      compareselectedchat = SelectedChat;
      KeepScrollUpChat(scroll);
    }

    if (!CheksEmptyObject(Error)) {
      toast({
        title: `Error:${Error}`,
        status: "error",
        duration: 2500,
        position: "top",
      });
    }
  }, [SelectedChat, Error, toast]);

  const HandleMessage = (e) => {
    if (e.key === "Enter" && NewMessage) {
      socket.emit("typing", SelectedChat._id);
      SendMessage(
        NewMessage,
        SelectedChat,
        socket,
        setMessgae,
        Message,
        dispatch
      );
      KeepScrollUpChat(scroll);
    }
  };

  useEffect(() => {
    KeepScrollUpChat(scroll);
    socket.on("messageRecieved", (recievedMessage) => {
     
      if (!SelectedChat || recievedMessage.chat._id !== SelectedChat._id) {
       
        if (!Notification.includes(recievedMessage)) {
        

          dispatch(initializeNotification([recievedMessage, ...Notification]));
        }
        //notification
      } else {
       
        setMessgae([...Message, recievedMessage]);
      }
    });
  });

  useEffect(() => {
    KeepScrollUpChat(scroll);
  }, [Message]);

  const TypingHandler = (e) => {
    setNewMessgae(e.target.value);
    if (!SocketConnected) return;

    if (!Typing) {
      setTypinge(true);
      socket.emit("typing", SelectedChat._id);
    }

    const time = 3000;
    const startTime = new Date().getTime();

    setTimeout(() => {
      const timeNow = new Date().getTime();
      const TimeDiff = timeNow - startTime;
      if (TimeDiff >= time && Typing) {
        setTypinge(false);
        socket.emit("stop typing", SelectedChat._id);
      }
    }, time);
  };

  const Back = () => {
    dispatch(initializeSelectedChat({}));
  };

  return (
    <Box
      display={{
        base: CheksEmptyObject(SelectedChat) ? "none" : "flex",
        md: "flex",
      }}
      alignItems="center"
      flexDir="column"
      p={3}
      bg="white"
      w={{ base: "100%", md: "68%" }}
      minH={{ base: "100%", md: "85vh" }}
      borderRadius="lg"
      borderWidth="1px"
    >
      {CheksEmptyObject(SelectedChat) ? (
        ""
      ) : (
        <Box
          w="100%"
          display="flex"
          alignItems="center"
          justifyContent="space-between"
          h="7vh"
          mb="2"
        >
          <Text display={{ md: "none" }} onClick={() => Back()}>
            <ArrowBackIcon boxSize={5} />
          </Text>
          <Text
            p="0em 2em"
            fontFamily="work sans"
            fontSize={{ base: "12", md: "20" }}
            fontWeight="medium"
          >
            {SelectedChat && SelectedChat.isGroupChat
              ? SelectedChat.chatName.toUpperCase()
              : GetNameofSender(User, SelectedChat.users).toUpperCase()}
          </Text>
          {SelectedChat.isGroupChat ? (
            <UpdateGroupChat>
              <ViewIcon />
            </UpdateGroupChat>
          ) : (
            <ViewChatDetails>
              <ViewIcon />
            </ViewChatDetails>
          )}
        </Box>
      )}
      <Box
        width="100%"
        background="lightgray"
        borderRadius="lg"
        height="90%"
        display={"flex"}
      >
        {Loading ? (
          <Box m={"auto"}>
            <ChatLoading />
          </Box>
        ) : !CheksEmptyObject(SelectedChat) ? (
          <Box
            w={"100%"}
            h="100%"
            display={"flex"}
            flexDirection={"column"}
            justifyContent={"space-between"}
          >
            <Box w={"100%"} h={"85%"} overflowY={"auto"} ref={scroll}>
              {
                <div
                  style={{
                    height: "auto",
                    weight: "100%",

                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "end",
                  }}
                >
                  {" "}
                  {Message ? (
                    Message.map((message, i) => {
                      return (
                        <MessageCompo
                          key={message._id}
                          index={i}
                          messages={Message}
                          message={message}
                        />
                      );
                    })
                  ) : (
                    <ChatLoading />
                  )}
                </div>
              }
            </Box>
            <FormControl
              flexShrink={0}
              flexFlow={1}
              onKeyDown={HandleMessage}
              display={"flex"}
              flexDirection={"column"}
              width={"100%"}
              h={"20%"}
            >
              {Typing ? !NewMessage && <div>Typing...</div> : ""}
              <Input
                mt={"auto"}
                type={"text"}
                background={"white"}
                h="60%"
                placeholder="Enter a message..."
                isRequired={true}
                flexShrink={0}
                onChange={TypingHandler}
              />
            </FormControl>
          </Box>
        ) : (
          ""
        )}
      </Box>
    </Box>
  );
};

export default ChatBox;
