import { createSlice ,configureStore} from '@reduxjs/toolkit'

const initialState = {
  ChatApp:{
      User:{},
      Users:[],
      CreatedChat:{},
      SelectedChat:{},
      ChatMessages:[],
      SelectedUser:[],
      Notification:[],
      Chats:[],
      Loading:false,
      Error:{}
  }
}

 const counterSlice = createSlice({
  name: 'ChatApp',
  initialState,
  reducers: {
   initializeUser(state,action){
       state.ChatApp.User = action.payload.user
       localStorage.setItem('user',JSON.stringify(action.payload.user ? action.payload.user : {}))
   },
   initializeUsers(state,action){
    state.ChatApp.Users = action.payload.users
   },
   initializeError(state,action){
       state.ChatApp.Error = action.payload
   },
   initializeLoding(state,action){
    state.ChatApp.Loading = action.payload
   },
   initializeCreatingChat(state,action){
     state.ChatApp.CreatedChat = action.payload;
   },
   initializeChats(state,action){
     state.ChatApp.Chats = action.payload;
   },
   initializeSelectedChat(state,action){
     state.ChatApp.SelectedChat = action.payload;
   },
   initializeSelectedUser(state,action){
     state.ChatApp.SelectedUser = action.payload;
   },
   initializeMessages(state,action){
    state.ChatApp.ChatMessages = action.payload;
  },
  initializeNotification(state,action){
    state.ChatApp.Notification = action.payload;
  },
  },
})

// Action creators are generated for each case reducer function
export const { initializeUser,initializeError,initializeNotification,initializeUsers,initializeMessages,initializeLoding,initializeSelectedUser,initializeSelectedChat,initializeChats,initializeCreatingChat } = counterSlice.actions

export const store = configureStore({
    reducer: counterSlice.reducer,
  });
