import React, { useEffect, useState } from "react";
import {
  Avatar,
  Box,
  Button,
  FormControl,
  Input,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalFooter,
  ModalHeader,
  ModalOverlay,
  Text,
  Toast,
  useDisclosure,
  useToast,
} from "@chakra-ui/react";
import { useSelector } from "react-redux";
import ChatLoading from "../ChatLoading";
import UserListItems from "./UserListItems";
import {
  initializeSelectedChat,
  initializeSelectedUser,
  initializeUsers,
} from "../../store/slice";
import UserBadgeItem from "../UserBadgeItem";
import { useDispatch } from "react-redux";
import {
  AdduUserToGroup,
  RemoveUserFromGroup,
  SearchUsers,
  UpdateGroupName,
} from "../../Apifunctions/API";

const UpdateGroupChat = ({ children }) => {
  const [Query, setSearchUser] = useState("");
  const [GroupName, setGroupName] = useState("");
  const { isOpen, onOpen, onClose } = useDisclosure();
  const { SelectedChat, Loading, Users, SelectedUser, Error } = useSelector(
    (state) => state.ChatApp
  );
  const User = JSON.parse(localStorage.getItem("user"));
  const dispatch = useDispatch();
  const toast = useToast();

  useEffect(() => {
    if (!isOpen) {
      dispatch(initializeSelectedUser([]));
      dispatch(initializeUsers([]));
      setSearchUser("");
    }
  }, [isOpen]);

  useEffect(() => {
    if (
      !Error &&
      Object.keys(Error).length !== 0 &&
      Object.getPrototypeOf(Error) !== Object.prototype
    ) {
      console.log();
      toast({
        title: `${Error}`,
        status: "warning",
        isClosable: true,
        duration: 2500,
        position: "top",
      });
    }
  }, [Error]);

  const AddToGroup = (addtogroupuser, selectedchat) => {
    if (SelectedUser.includes(addtogroupuser)) {
      toast({
        title: "You already selected this user !",
        status: "warning",
        isClosable: true,
        duration: 2500,
        position: "top",
      });
      return;
    }

    if (
      selectedchat.users.some(
        (user) => JSON.stringify(user) === JSON.stringify(addtogroupuser)
      )
    ) {
      toast({
        title: "You already selected this user !",
        status: "warning",
        isClosable: true,
        duration: 2500,
        position: "top",
      });
      return;
    }

    toast({
      title: "Added !",
      status: "success",
      isClosable: true,
      duration: 2500,
      position: "top",
    });
    AdduUserToGroup(addtogroupuser, SelectedChat, dispatch);
  };

  const HandleSearchUser = (query) => {
    setSearchUser(query);
    SearchUsers(query, dispatch);
  };

  const RemoveUser = (user) => {
    const AfterRemove = SelectedUser.filter((u) => u._id !== user._id);
    dispatch(initializeSelectedUser(AfterRemove));
    RemoveUserFromGroup(user, SelectedChat, dispatch);
  };

  const HandleUpdate = () => {
    UpdateGroupName(GroupName, SelectedChat, dispatch);
    onClose()
  };

  return (
    <div>
      <Button mr={"4"} onClick={onOpen}>
        {children}
      </Button>

      <Modal isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader textAlign={"center"}>
            {SelectedChat.chatName.toUpperCase()}
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody display={"flex"} flexDirection="column">
            {SelectedChat && (
              <Box
                display={"flex"}
                p="2vmax 0vmax"
                overflowX={"scroll"}
                alignItems={"center"}
                w="100%"
              >
                {SelectedChat.users?.map((u) => {
                  return (
                    <UserBadgeItem
                      User={u}
                      key={u._id}
                      HandleClick={() => RemoveUser(u)}
                    />
                  );
                })}
              </Box>
            )}

            <FormControl>
              <Input
                type={"text"}
                mb="4"
                value={GroupName}
                onChange={(e) => setGroupName(e.target.value)}
                placeholder="Enter Group Name"
              />
            </FormControl>
            <FormControl>
              <Input
                type={"text"}
                value={Query}
                onChange={(e) => HandleSearchUser(e.target.value)}
                placeholder="Search Users e.g joe Doe, Hemant Sonawane"
              />
            </FormControl>

            {!Loading ? (
              Users?.map((user, i) => {
                return (
                  <UserListItems
                    key={i}
                    User={user}
                    HandleClick={() => AddToGroup(user, SelectedChat)}
                  />
                );
              })
            ) : (
              <ChatLoading />
            )}
            <ModalFooter p={"1vmax 0px 1vmax 0px"}>
              <Button colorScheme="blue" onClick={() => HandleUpdate()}>
                Update Chat
              </Button>
            </ModalFooter>
          </ModalBody>
        </ModalContent>
      </Modal>
    </div>
  );
};

export default UpdateGroupChat;
