import { Spinner } from '@chakra-ui/react'
import React from 'react'

const ChatLoading = () => {
  return (
    <div style={{display:'flex',justifyContent:"center" , alignItems:"center"}} ><Spinner
    thickness='2px'
    speed='0.65s'
    emptyColor='gray.200'
    color='blue.500'
    size='xl'
  /></div>
  )
}

export default ChatLoading