import { Avatar, Box, Text } from "@chakra-ui/react";
import React from "react";

const UserListItems = ({ User,HandleClick }) => {
  return (
    <>
      <Box onClick={HandleClick} cursor="pointer" display={"flex"} borderRadius='md' backgroundColor='#d6d6d6' alignItems="center"   padding={'4'} mt="3" >
        <Avatar boxSize={'10'} name={User.name} src={User.pic?.url} />
        <Box display={"flex"} flexDirection={"column"} ml='2' >
          <Text  fontSize={'bold'} fontWeight='bold' fontFamily='work sans' >Name: {User.name}</Text>
          <Text  fontSize={'smaller'} fontWeight='black'  fontFamily='work sans' >Email: {User.email}</Text>
        </Box>
      </Box>
    </>
  );
};

export default UserListItems;
