import axios from "axios";
import {
  initializeUser,
  initializeError,
  initializeUsers,
  initializeLoding,
  initializeCreatingChat,
  initializeChats,
  initializeSelectedChat,
  initializeMessages,
} from "../store/slice";

export const SignUpUser = async (data, dispatch) => {
  try {
    const config = {
      headers: { "Content-Type": "application/json" },
      withCredentials: true,
    };

    const Data = await axios.post("/api/v1", data, config);
    dispatch(initializeUser(Data.data));
  } catch (error) {
    dispatch(initializeError(error.response.data.error));
  }
};

export const SearchUsers = async (data, dispatch) => {
  try {
    dispatch(initializeLoding(true));
    const config = {
      headers: { "Content-Type": "application/json" },
      params: {
        search: data,
      },
      withCredentials: true,
    };
    const Data = await axios.get("/api/v1/users", config);
    dispatch(initializeUsers(Data.data));
  } catch (error) {
    dispatch(initializeError(error.response.data.error));
  }
};

export const LogInUser = async (data, dispatch) => {
  try {
    const config = {
      headers: { "Content-Type": "application/json" },
      withCredentials: true,
    };
    const Data = await axios.post("/api/v1/login", data, config);
    dispatch(initializeUser(Data.data));
  } catch (error) {
    dispatch(initializeError(error.response.data.error));
  }
};

export const accsessChat = async (data, dispatch, Chats) => {
  try {
    dispatch(initializeLoding(true));
    const config = {
      headers: { "Content-Type": "application/json" },
      withCredentials: true,
    };
    const Data = await axios.post(
      "/api/v1/accesschat",
      { userId: data },
      config
    );
    dispatch(initializeCreatingChat(Data.data));
    if (!Chats.find((chat) => chat._id === Data.data._id)) {
      dispatch(initializeChats([...Chats, Data.data]));
    }
    dispatch(initializeLoding(false));
  } catch (error) {
    dispatch(initializeError(error.response.data.error));
  }
};

export const FatchMyChats = async (dispatch) => {
  try {
    dispatch(initializeLoding(true));
    const config = {
      headers: { "Content-Type": "application/json" },
      withCredentials: true,
    };
    const Data = await axios.get("/api/v1/fetchchats", config);
    dispatch(initializeChats(Data.data));
    dispatch(initializeLoding(false));
  } catch (error) {
    dispatch(initializeError(error.response.data.error));
  }
};

export const CreateGroup = async (
  users,
  name,
  SelectedChats,
  dispatch,
  onClose
) => {
  try {
    dispatch(initializeLoding(true));
    onClose();
    const config = {
      headers: { "Content-Type": "application/json" },
      withCredentials: true,
    };
    const POSTDATA = {
      name: name,
      users: users.map((u) => u._id),
    };
    const Data = await axios.post("/api/v1/creategroupchat", POSTDATA, config);
    dispatch(initializeChats([Data.data, ...SelectedChats]));
    dispatch(initializeLoding(false));
  } catch (error) {
    dispatch(initializeError(error.response.data.error));
  }
};

export const AdduUserToGroup = async (
  user,
  selectedchat,
  dispatch,
  onClose
) => {
  try {
    // dispatch(initializeLoding(true));
    const config = {
      headers: { "Content-Type": "application/json" },
      withCredentials: true,
    };

    const POSTDATA = {
      chatId: selectedchat._id,
      userId: user._id,
    };

    const Data = await axios.put("/api/v1/addtogroup", POSTDATA, config);
    dispatch(initializeSelectedChat(Data.data));
  } catch (error) {
    dispatch(initializeError(error.response.data.error));
  }
};

