import {
  Avatar,
  Box,
  Button,
  Drawer,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  Input,
  Menu,
  MenuButton,
  MenuDivider,
  MenuItem,
  MenuList,
  Text,
  Tooltip,
  useDisclosure,
  useToast,
} from "@chakra-ui/react";
import { ChevronDownIcon, BellIcon } from "@chakra-ui/icons";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Profile from "../Profile";
import { useNavigate } from "react-router-dom";
import {
  initializeLoding,
  initializeNotification,
  initializeSelectedChat,
  initializeUser,
  initializeUsers,
} from "../../store/slice";
import { accsessChat, SearchUsers } from "../../Apifunctions/API";
import LoadingCompo from "../LoadingCompo";
import UserListItems from "./UserListItems";
import { CheksEmptyObject } from "../../Functions/fun";
import NotificationBadge from 'react-notification-badge';
import {Effect} from 'react-notification-badge';



const SideDrawer = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [Search, SetSearch] = useState("");
  const btnRef = React.useRef();

  let user = useSelector((state) => state.ChatApp.User);
  if (user !== {}) user = JSON.parse(localStorage.getItem("user"));

  const { Loading, Users, Error, Chats, Notification } = useSelector(
    (state) => state.ChatApp
  );

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const toast = useToast();

  useEffect(() => {
    if (!CheksEmptyObject(Error) && Search) {
      toast({
        title: "Fail to Search Users !",
        status: "error",
        duration: 2500,
        isClosable: true,
        position: "top-left",
      });
      return;
    }
    if (Users?.length) {
      dispatch(initializeLoding(false));
    }
  }, [toast, Error, Users, dispatch]);

  useEffect(() => {
    if (!isOpen) {
      dispatch(initializeUsers([]));
    }
  }, [isOpen]);

  const LogOutHandler = () => {
    dispatch(initializeUser([]));
    navigate("/");
  };

  const HandleSearch = () => {
    if (!Search) {
      toast({
        title: "Please Enter Sommting in your Search !",
        status: "warning",
        duration: 2500,
        isClosable: true,
        position: "top-left",
      });
      return;
    }
    SearchUsers(Search, dispatch);
  };

  return (
    <div>
      <Box
        display="flex"
        justifyContent="space-between"
        alignItems="center"
        bg="white"
        maxW="100vw"
        p="5px 10px 5px 10px"
        borderWidth="5px"
      >
        <Tooltip label="Search Users to chat" hasArrow placement="bottom-end">
          <Button variant="ghost" onClick={onOpen}>
            <i className="fas fa-search"></i>
            <Text display={{ base: "none", md: "flex" }} px={4}>
              Search User
            </Text>
          </Button>
        </Tooltip>
        <Text
          ml={"auto"}
          fontSize={{ base: "medium", md: "2xl" }}
          fontFamily="Work sans"
        >
          Talk-A-Tive
        </Text>

        <Menu>
         
          <MenuButton ml={"auto"} p={1} >
          <NotificationBadge count={Notification.length} effect={Effect.SCALE} />
            <BellIcon boxSize={6} />
          </MenuButton>

      
          
          {Notification.length ? (
            <MenuList>
              {Notification.map((noty) => {
                return (
                  <MenuItem
                    textOverflow={"ellipsis"}
                    whiteSpace={"nowrap"}
                    fontWeight={"semibold"}
                    fontFamily={"work sans"}
                    key={noty._id}
                    onClick={()=> {
                      dispatch(initializeSelectedChat(noty.chat))
                      dispatch(initializeNotification(Notification.filter((n)=> n._id !== noty._id )))
                    }}
                  >
                    {noty.chat.isGroupChat
                      ? `New Messgae in:${noty.chat.chatName}`
                      : `Messgae From ${noty.sender.name}`}
                  </MenuItem>
                );
              })}
            </MenuList>
          ) : (
            <MenuList textAlign={"center"}>{` No New Messages`}</MenuList>
          )}
        </Menu>
        <Menu>
          <MenuButton as={Button} bg="white" rightIcon={<ChevronDownIcon />}>
            <Avatar
              size="sm"
              cursor="pointer"
              name={user.name}
              src={user.pic?.url}
            />
          </MenuButton>
          <MenuList>
            <Profile User={user}>
              <MenuItem>My Profile</MenuItem>
            </Profile>
            <MenuDivider />
            <MenuItem onClick={() => LogOutHandler()}>Logout</MenuItem>
          </MenuList>
        </Menu>
      </Box>
      <Drawer
        isOpen={isOpen}
        placement="left"
        onClose={onClose}
        finalFocusRef={btnRef}
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerCloseButton />
          <DrawerHeader>Search Users</DrawerHeader>

          <DrawerBody>
            <Box
              display={"flex"}
              alignItems={"center"}
              justifyContent="space-between"
            >
              <Input
                value={Search}
                placeholder="Search by Name or Email"
                onChange={(e) => SetSearch(e.target.value)}
              />
              <Button onClick={() => HandleSearch()}>Go</Button>
            </Box>

            <Box mt={"4"}>
              {Loading ? (
                <LoadingCompo />
              ) : (
                Users?.map((user) => {
                  return (
                    <UserListItems
                      key={user._id}
                      User={user}
                      HandleClick={() => {
                        accsessChat(user._id, dispatch, Chats);
                      }}
                    />
                  );
                })
              )}
            </Box>
          </DrawerBody>

          <DrawerFooter>
            <Button variant="outline" onClick={onClose}>
              Cancel
            </Button>
          </DrawerFooter>
        </DrawerContent>
      </Drawer>
    </div>
  );
};

export default SideDrawer;
