import {
  Avatar,
  Box,
  Button,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  Text,
  useDisclosure,
} from "@chakra-ui/react";
import React from "react";
import { useSelector } from "react-redux";
import {
  GetChatOrGroupImg,
  getSender,
  getSenderEmail,
} from "../../Functions/fun";

const ViewChatDetails = ({ children }) => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const { SelectedChat } = useSelector((state) => state.ChatApp);
  const User = JSON.parse(localStorage.getItem("user"));

  return (
    <div>
      <Button mr={"4"} onClick={onOpen}>
        {children}
      </Button>

      <Modal isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader textAlign={"center"}>
            {getSender(User, SelectedChat.users)}
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody
            display={"flex"}
            flexDirection="column"
            alignItems={"center"}
          >
            <Avatar
              boxSize={"20"}
              name="Dan Abrahmov"
              src={GetChatOrGroupImg(SelectedChat, User)}
            />
            <Box width={"100%"} p="2" textAlign="center">
              <Text mt={"2"} fontFamily={"work sans"} fontWeight={"semibold"}>
                Email:{getSenderEmail(User, SelectedChat.users)}
              </Text>
            </Box>
          </ModalBody>
        </ModalContent>
      </Modal>
    </div>
  );
};

export default ViewChatDetails;
