import {
  Button,
  FormControl,
  FormLabel,
  Input,
  InputGroup,
  InputRightElement,
  useToast,
  VStack,
} from "@chakra-ui/react";
import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { LogInUser  } from "../../Apifunctions/API";
import { useDispatch, useSelector } from "react-redux";
import {CheksEmptyObject} from '../../Functions/fun'
import { initializeError } from "../../store/slice";


const Login = () => {
  const [Show, SetShow] = useState(false);
  const [Email, SetEmail] = useState("");
  const [Password, SetPassword] = useState("");
  const [Current, SetCurrent] = useState(false);
  const toast = useToast();
  const { Error } = useSelector((state) => state.ChatApp);
  const User = JSON.parse(localStorage.getItem('user'));
  const navigate = useNavigate();
  const dispatch = useDispatch();

  useEffect(() => {
    if (User?._id) {
      navigate("/chats");
      return;
    } else if ( !CheksEmptyObject(Error) && Current) {
      toast({
        title: "Error",
        description: `${Error}`,
        status: "error",
        duration: 2500,
        isClosable: true,
      });
      dispatch(initializeError({}))
    }
  }, [User, Error, navigate, toast, Current]);

  const SubmitHandler = () => {
    if (!Email || !Password) {
      toast({
        title: "Error",
        description: `Please Enter All Fields`,
        status: "warning",
        duration: 2500,
        isClosable: true,
      });
      return;
    }
    SetCurrent(true);

    const data = {
      email: Email,
      password: Password,
    };
    LogInUser(data, dispatch);
  };
  return (
    <VStack spacing={4}>
      <FormControl id="first-email" isRequired>
        <FormLabel>Email</FormLabel>
        <Input
          placeholder="Enter your Email"
          type={"email"}
          value={Email}
          onChange={(e) => SetEmail(e.target.value)}
        />
      </FormControl>

      <FormControl id="first-password" isRequired>
        <FormLabel>Password</FormLabel>
        <InputGroup>
          <Input
            placeholder="Enter your Password"
            type={Show ? "text" : "password"}
            value={Password}
            onChange={(e) => SetPassword(e.target.value)}
          />
          <InputRightElement width={"4rem"}>
            <Button
              fontFamily={"work sans"}
              onClick={() => SetShow((state) => !state)}
            >
              {Show ? "Hide" : "Show"}
            </Button>
          </InputRightElement>
        </InputGroup>
      </FormControl>

      <Button
        colorScheme={"blue"}
        onClick={() => SubmitHandler()}
        color="white"
        width={"100%"}
        style={{ marginTop: 15 }}

      >
        Login
      </Button>
      <Button
        colorScheme={"red"}
        onClick={() => {
          SetEmail("guest@gmail.com");
          SetPassword("123456");
        }}
        color="white"
        width={"100%"}
        style={{ marginTop: 15 }}
      >
        Get Guest User Credentials
      </Button>
    </VStack>
  );
};

export default Login;
